@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400;500;700&display=swap);
.loader {
	color: #ffffff;
	font-size: 90px;
	text-indent: -9999em;
	overflow: hidden;
	width: 1em;
	height: 1em;
	border-radius: 50%;
	margin: 72px auto;
	position: relative;
	-webkit-transform: translateZ(0);
	transform: translateZ(0);
	-webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
	animation: load6 1.7s infinite ease, round 1.7s infinite ease;
}
@-webkit-keyframes load6 {
	0% {
		box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em,
			0 -0.83em 0 -0.477em;
	}
	5%,
	95% {
		box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em,
			0 -0.83em 0 -0.477em;
	}
	10%,
	59% {
		box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em,
			-0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
	}
	20% {
		box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em,
			-0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
	}
	38% {
		box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em,
			-0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
	}
	100% {
		box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em,
			0 -0.83em 0 -0.477em;
	}
}
@keyframes load6 {
	0% {
		box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em,
			0 -0.83em 0 -0.477em;
	}
	5%,
	95% {
		box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em,
			0 -0.83em 0 -0.477em;
	}
	10%,
	59% {
		box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em,
			-0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
	}
	20% {
		box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em,
			-0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
	}
	38% {
		box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em,
			-0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
	}
	100% {
		box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em,
			0 -0.83em 0 -0.477em;
	}
}
@-webkit-keyframes round {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
@keyframes round {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.content{width:80%;-webkit-flex-basis:80%;flex-basis:80%;position:relative;z-index:1;padding:50px 0}.weather{-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(255,255,255,.25);border-radius:20px;max-width:80%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;padding:3rem 4rem;margin:0 auto;color:#fff;text-shadow:1px 1px 2px #000}.weather:not(:last-child){margin-bottom:50px}.weather__hello{text-align:center;width:100%;font-size:1.5rem}.weather__city{font-weight:500;margin:0 0 10px;font-size:1.5rem}.weather__temp{font-weight:700;font-size:5rem}.weather__day-temp{text-align:center}.weather__date{font-weight:200}.today__item{-webkit-flex:1 1;flex:1 1}.today__item__title{font-weight:300}.today__item__value{font-size:1.2rem;font-weight:500;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.wind-icon{width:16px;height:16px;-webkit-transform-origin:center;transform-origin:center;-webkit-filter:drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.9));filter:drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.9));margin-top:-4px;position:relative}.wind-icon__wrap{width:20px;margin-right:3px}@media(max-width: 1023px){.content{width:70%}.weather{margin:0 40px;padding:2rem 2.6rem}.weather__temp{font-size:3rem}.weather__city{font-size:1.3rem}.weather__status .icon{width:70px;height:70px}.weather__status .icon img{width:100%}.today__item__value{font-size:.9rem;margin-top:4px}}@media(max-width: 767px){.content{width:100%;padding:80px 0}.weather__hello{font-size:1.3rem}}@media(max-width: 500px){.weather{padding:1.4rem 1.7rem}.today{-webkit-flex-wrap:wrap;flex-wrap:wrap}.today__item{width:50%;-webkit-flex:none;flex:none}.today__item:nth-child(1){margin-bottom:16px}}@media(max-width: 390px){.weather{display:block;text-align:center}.weather__status .icon{margin:0 auto}.today__item{width:100%;text-align:center;margin-bottom:16px}.today__item__v