.city{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;width:100%;border:none;padding:12px 6px;color:#fff;text-shadow:1px 1px 2px #000;background-color:transparent}.city__title{font-size:1rem;-webkit-align-items:center;align-items:center;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;padding-right:4px}.city__title svg{margin-right:10px;vertical-align:middle}.city__temp{font-size:1.3rem;-webkit-flex-shrink:0;flex-shrink:0;margin-left:10px}.city__location{-webkit-filter:drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.7));filter:drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.7))}.city__wrap{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;background:linear-gradient(rgba(211, 211, 211, 0.6), rgba(173, 173, 173, 0.4))}.city__wrap:hover{background:linear-gradient(rgba(211, 211, 211, 0.8), rgba(173, 173, 173, 0.6))}.city__wrap.editable .city__delete{margin-left:0}.city__delete{width:24px;height:24px;background-color:transparent;border:none;padding:2px;position:relative;margin-left:-24px;transition:all .3s;opacity:1}.city__delete:hover{opacity:.8}.city__delete img{width:20px;height:20px}
.cities{padding-bottom:20px}.cities__edit{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin-top:14px}.cities__edit .edit{background:linear-gradient(rgba(211, 211, 211, 0.6), rgba(173, 173, 173, 0.4));border-radius:20px;border:none;padding:6px 20px;color:#fff;text-shadow:1px 1px 2px #000;width:86px}.cities__edit .edit:hover{background:linear-gradient(rgba(211, 211, 211, 0.8), rgba(173, 173, 173, 0.6))}@media(max-width: 767px){.cities{overflow-y:auto}}
.sidebar{width:20%;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(255,255,255,.25);position:relative;z-index:1;padding-bottom:20px}.sidebar__search{margin-bottom:14px;padding:10px 0}.sidebar__search__input{background-color:transparent;width:100%;border:none;border-bottom:1px solid #fff;height:47px;color:#fff;text-shadow:1px 1px 1px #313131;padding:3px 6px}.sidebar__search__input::-webkit-input-placeholder{color:#fff;text-shadow:1px 1px 1px #313131}.sidebar__search__input:-ms-input-placeholder{color:#fff;text-shadow:1px 1px 1px #313131}.sidebar__search__input::placeholder{color:#fff;text-shadow:1px 1px 1px #313131}@media(max-width: 1023px){.sidebar{width:30%}.city__title{font-size:.9rem}.city__temp{font-size:1.1rem}}@media(max-width: 767px){.sidebar{position:fixed;top:0;left:-40%;z-index:2;height:100vh;overflow-y:auto;width:40%;transition:all .3s ease}.sidebar.visible{left:0}}@media(max-width: 500px){.sidebar{width:60%;left:-60%}}@media(max-width: 390px){.sidebar{width:80%;left:-80%}}
body{font-family:"Raleway",sans-serif;font-size:16px}h1,h2,h3,h4,h5,h6{margin:0;font-weight:400}button{cursor:pointer}button:active,button:focus,input:active,input:focus,a:active,a:focus{outline:#2f78ff solid 2px}input{box-sizing:border-box}.app{display:-webkit-flex;display:flex;-webkit-align-items:stretch;align-items:stretch;background-color:#063499;width:100%;min-height:100vh;position:relative}.bg{position:fixed;left:0;top:0;width:100%;height:100%;object-fit:cover;z-index:0}@media(max-width: 1023px){body{font-size:14px}}@media(max-width: 767px){.app{display:block}}@media(max-width: 390px){body{font-size:12px}}
.menu-button{display:none;width:50px;height:50px;position:fixed;right:0;top:0;background-color:transparent;border:none;margin:0;padding:0;z-index:2}.menu-button svg{transition:-webkit-transform 400ms;transition:transform 400ms;transition:transform 400ms, -webkit-transform 400ms;width:50px;height:50px;-webkit-filter:drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.9));filter:drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.9))}.menu-button svg .line{fill:none;transition:stroke-dasharray 400ms,stroke-dashoffset 400ms;stroke:#fff;stroke-width:5.5;stroke-linecap:round}.menu-button svg .top,.menu-button svg .bottom{stroke-dasharray:40 121}.menu-button.active{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.menu-button.active .top,.menu-button.active .bottom{stroke-dashoffset:-68px}@media(max-width: 767px){.menu-button{display:block}}
